cc.Class({
    extends: cc.Component,

    properties: {
        // foo: {
        //     // ATTRIBUTES:
        //     default: null,        // The default value will be used only when the component attaching
        //                           // to a node for the first time
        //     type: cc.SpriteFrame, // optional, default is typeof default
        //     serializable: true,   // optional, default is true
        // },
        // bar: {
        //     get () {
        //         return this._bar;
        //     },
        //     set (value) {
        //         this._bar = value;
        //     }
        // },
    },

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {},
    ClickTab() {
        this.node.destroy();
        this.loaderViewText()
    },
    start() {

    },
    loaderViewText() {
        cc.loader.loadRes("/prefab/iView", (err, iView) => {
            if (err) {
                console.log(err)
                return;
            }
            var newNode = cc.instantiate(iView);
            let _h = parseInt(cc.director.getScene().getChildByName('Canvas').height/2)
            let _w = parseInt(cc.director.getScene().getChildByName('Canvas').width/2)
            newNode.setPosition(_w,_h)
            cc.director.getScene().addChild(newNode);

        });
    },
    // update (dt) {},
});
